import Swal from "sweetalert2";
import {
  React,
  connect,
  Link,
  reduxForm,
  Panel,
  PanelHeader,
  PanelBody,
  Tabel,
  ToastNotification,
  getItem,
  setItem,
  uuidv4,
  Loading,
} from "../../../components/helpers/library";
import { postDataGrosir, postDataNoTImeOut2 } from "../../../config/axios";
import HeadPenerimaanBarang from "./HeadPenerimaanBarang";

class PenerimaanBaarang extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      columns: [
        {
          dataField: "no_suplier",
          text: "No Pengiriman",
        },
        {
          dataField: "kode_group",
          text: "Kategori",
        },
        {
          dataField: "kode_jenis",
          text: "Jenis",
        },
        {
          dataField: "berat",
          text: "Berat",
          headerClasses: "text-right",
          formatter: (cell) => {
            return <div className="text-right"> {cell} </div>;
          },
        },
        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row, index) => {
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    type="button"
                    onClick={() => this.hapusdata(index)}
                    className="btn btn-danger mr-3"
                  >
                    <i className="fa fa-trash"></i>
                  </button>
                </div>
              </div>
            );
          },
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    // this.print = this.print.bind(this);
  }

  handleSubmit(data) {
    if (data.kode_group === undefined) {
      ToastNotification("info", "Kategori Harus Dipilih");
      return false;
    }
    if (data.jenis === undefined) {
      ToastNotification("info", "Jenis Harus Dipilih");
      return false;
    }
    if (data.bukan_barang_supliar === true) {
      if (data.berat === undefined) {
        ToastNotification("info", "Berat Tida Boleh Kosong");
        return false;
      }
    }

    let hasil = {
      id: uuidv4(),
      no_suplier: data.no_pengiriman,
      kode_group: data.kode_group,
      kode_jenis: data.jenis,
      berat: data.berat === undefined ? 0 : parseFloat(data.berat),
      type: data.bukan_barang_supliar === true ? "OWNER" : "SUPPLIER",
    };

    let cek =
      getItem("data_terima_barang").length === 0
        ? []
        : getItem("data_terima_barang");
    let hasil_cek = cek.findIndex((hasil) => hasil.no_suplier === data.no_pengiriman);
    
    if (hasil_cek === -1) {
      cek.push(hasil);
      setItem("data_terima_barang", cek);
      this.setState({
        berhasi: true,
      });
    }else{
      let cekkategori = cek.findIndex((hasil) => hasil.kode_group === data.kode_group);
      if(cekkategori === -1){
          cek.push(hasil);
          setItem("data_terima_barang", cek);
          this.setState({
            berhasi: true,
          });
      }else{
        let cekjenis = cek.findIndex((hasil) => hasil.kode_jenis === data.jenis);
        if(cekjenis === -1){
          cek.push(hasil);
          setItem("data_terima_barang", cek);
          this.setState({
            berhasi: true,
          });
        }else{
          ToastNotification('info','No Pengiriman Sudah Ada Ditable')
        }
      }
    }
  }
  hapusdata(id) {
    Swal.fire({
      title: "Anda Yakin !!",
      text: "Ingin Menghapus Data Ini ?",
      icon: "warning",
      position: "top-center",
      cancelButtonText: "Tidak",
      showCancelButton: true,
      confirmButtonText: "OK",
      showConfirmButton: true,
    }).then((result) => {
      if (result.isConfirmed) {
        let data = JSON.parse(localStorage.getItem("data_terima_barang"));
        data.splice(id, 1);
        setItem("data_terima_barang", data);
        this.setState({
          berhasi: false,
        });
        // deleteLocalItemBarcode("data_barang_tmp", id);
        // this.props.dispatch(getListReturnSupplier());
      }
    });
  }

  kirimbarang() {
    let data = getItem("data_terima_barang");
    this.setState({
        isLoading : true
    })
    let databarang = [];
    data.forEach((row) => {
      let hasil = {
        no_suplier: row.no_suplier,
        kode_group: row.kode_group,
        kode_jenis: row.kode_jenis,
        berat: row.berat,
        type: row.type,
      };
      databarang.push(hasil);
    });

    postDataNoTImeOut2("penerimaan-suplier", databarang)
      .then((res) => {
        setItem('data_terima_barang',[])
        this.setState({
            isLoading : false
        })
        
        postDataGrosir('kirim-stock/post/terima-stock/'+data.no_suplier).then((res)=>{
          console.log(res);
        })
        ToastNotification("success", res.data.message);

        window.location.reload()
      })

      .catch((err) => {
          console.log(err);
        this.setState({
            isLoading : false
        })
        ToastNotification("info", err.response?.data.message);
      });
  }
  componentDidMount() {}

  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Barang</Link>
          </li>
          <li className="breadcrumb-item active">Penerimaan Barang</li>
        </ol>
        <h1 className="page-header">Penerimaan Barang </h1>
        <Panel>
          <PanelHeader>Penerimaan Barang</PanelHeader>
          <PanelBody>
            <HeadPenerimaanBarang
              onSubmit={(data) => this.handleSubmit(data)}
            />
            <Tabel
              keyField="id"
              exportCsv={false}
              pagination="off"
              data={getItem("data_terima_barang") || []}
              columns={this.state.columns}
              empty={true}
              textEmpty="Data Barang Kosong"
            />
            <div className="text-right">
              <button
                className="btn btn-primary"
                disabled={this.state.isLoading}
                onClick={() => this.kirimbarang()}
              >
                 {this.state.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang Menyimpan
                </>
              ) : (
                "Simpan"
              )}
              </button>
            </div>
          </PanelBody>
        </Panel>
        {this.state.isLoading && (
          <Loading
            loading
            background="rgba(0, 0, 0, 0.35)"
            loaderColor="rgba(94, 147, 117, 1)"
          />
        )}
      </div>
    );
  }
}

PenerimaanBaarang = reduxForm({
  form: "PenerimaanBaarang",
  enableReinitialize: true,
})(PenerimaanBaarang);
export default connect()(PenerimaanBaarang);
