export const getDataKas = (currentIndex,sizePerPage) => {
  return (dispatch) => {
    getDataNoTimeOut(`cash/paginated?skip=${currentIndex}&limit=${sizePerPage}`).then((res) => {
      dispatch({
        type: GET_DATA_KAS,
        payload: {
          data: res.data,
        },
      })
    }).catch((err) => {
    });
  };
};
