export const getDataGudang = (data) => {
  return (dispatch) => {
    if (data === "master") {
      getData("gudang/get/all/master")
        .then((res) => {
          dispatch({
            type: GET_DATA_GUDANG,
            payload: {
              data: res.data,
            },
          });
        })
        .catch((err) => {
          console.log(err);
        });
    } else {
      getData("gudang/get/all")
        .then((res) => {
          dispatch({
            type: GET_DATA_GUDANG,
            payload: {
              data: res.data,
            },
          });
        })
        .catch((err) => {
          console.log(err);
        });
    }
  };
};

