export function getData(endpoint) {
  clearTimeout(timeout_getData)
  let config = {
    headers: { "x-auth-token": localStoragedecryp("token") },
    timeout: 5000
  };
  return new Promise((resolve, reject) => {
    timeout_getData = setTimeout(() => {
      Swal.fire({
        position: "center",
        imageUrl: require("../assets/img/LostConnection.gif"),
        title: "Tidak ada koneksi internet",
        text:"Silahkan Coba Lagi",
        confirmButtonText:"Coba Lagi",
        confirmButtonColor:"#558776",
        imageWidth: 250,
        imageHeight: 250,
        showConfirmButton: true,
        allowOutsideClick: false,
      }).then((result) => {
        if (result.isConfirmed) {
          window.location.reload()
        };
      })
      reject("TIMEOUT") 
  }, 5000);
    Axios.get(server + endpoint, config)
      .then((res) => {
        clearTimeout(timeout_getData)
        resolve(res);
      })
      .catch((err) => {
        if(Axios.isCancel(err)){
          clearTimeout(timeout_getData)
          reject(err)
        } else {
          var hasil = /Invalid Token./i.test(err && err.response && err.response.data)
          if(hasil){
            window.location.reload();
            localStorage.clear();
            localStorage.setItem("islogin", "false");
          } else {
            clearTimeout(timeout_getData)
            reject(err)
          }
        }
      });
  });
}
