
import {
    React,
    connect,
    Link,
    reduxForm,
    Panel,
    PanelHeader,
    PanelBody,
    getDataNoTimeOut2,
    formatDate,
    moment,
    ToastNotification,
    localStorageencryp,
  } from "../../../../components/helpers/library";
import HeadLaporanTerimaBarang from "./HeadLaporanTerimaBarang";
  
  
  class LaporanTerimaBarangSupplier extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        isLoading: false,
        export: false,
        databarang : [],
      };
      this.handleSubmit = this.handleSubmit.bind(this);
      // this.print = this.print.bind(this);
    }
  
    handleSubmit(data) {
      this.setState({
        isLoading: true,
      });
      let data_post = {
        tgl_awal: formatDate(moment(new Date(data.tgl_awal)).format('YYYY-MM-DD')),
        tgl_akhir: formatDate(moment(new Date(data.tgl_akhir)).format('YYYY-MM-DD')),
        kode_dept : data.kode_dept,
      };
      getDataNoTimeOut2(`penerimaan-suplier/reports?tgl_to=${data_post.tgl_akhir}&tgl_from=${data_post.tgl_akhir}&kode_dept=${data_post.kode_dept}`).then((res)=>{
        // console.log(res.data);
        this.setState({
          isLoading: false,
        });
        if(res.data.length === 0){
          ToastNotification('info', 'Laporan Terima Barang Kosong')
          this.setState({
            isLoading: false,
          });
          return false
        }
        localStorageencryp("tgl_laporan", JSON.stringify(data_post));

        this.setState({
          databarang : [],
        })
        this.setState({
          export : true,
          databarang : res.data
        })


      }).catch((er)=>{
        ToastNotification('info', 'Laporan Terima Barang Kosong')
        this.setState({
          export : false,
          isLoading: false,
        });
      })
     
    }
  
    componentDidMount() {
     
    }
    
    render() {
      return (
        <div>
          <ol className="breadcrumb float-xl-right">
            <li className="breadcrumb-item">
              <Link to="#">Laporan Barang</Link>
            </li>
            <li className="breadcrumb-item active">Laporan Terima Barang</li>
          </ol>
          <h1 className="page-header">Laporan Terima Barang </h1>
          <Panel>
            <PanelHeader>Laporan Terima Barang</PanelHeader>
            <PanelBody>
            <HeadLaporanTerimaBarang export={this.state.export} databarang={this.state.databarang} isLoading={this.state.isLoading} onSubmit={(data)=>this.handleSubmit(data)} />
            </PanelBody>
          </Panel>
        </div>
      );
    }
  }
  
  LaporanTerimaBarangSupplier = reduxForm({
    form: "LaporanTerimaBarangSupplier",
    enableReinitialize: true,
  })(LaporanTerimaBarangSupplier);
  export default connect()(LaporanTerimaBarangSupplier);