import React, { Component } from "react";

import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { InputDate, ReanderSelect } from "../../../../components/helpers/field";
import { getTglSystem } from "../../../../components/helpers/function";
import Tabel from "../../../../components/helpers/tabel";
import ValidasiTanggalLaporan from "../../../../Validasi/ValidasiTanggalLaporan";
import Loading from "react-fullscreen-loading";
import { getDataJenis } from "../../../../actions/datamaster_action";
import LaporanTerimaBarangSupplier from "./pdf/LaporanTerimaBarangSupplier";
import LaporanTerimaBarangSupplierExel from "./exel/LaporanTerimaBarangSupplierExel";

class HeadLaporanTerimaBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    this.setStartDate = this.setStartDate.bind(this);
    this.setLastDate = this.setLastDate.bind(this);
  }

  handleSubmit(data) {
    console.log(data);
  }
  exportPdf() {
    LaporanTerimaBarangSupplier(this.props.databarang);
  }
  setStartDate(date) {
    this.setState({
      tgl_awal: new Date(date),
    });
  }
  setLastDate(date) {
    this.setState({
      tgl_akhir: new Date(date),
    });
  }

  componentDidMount() {
    this.props.dispatch(getDataJenis());
    this.props.change("kode_dept", "ALL");
  }

  render() {
    const DataJenis = [
      {
        value: "ALL",
        name: "SEMUA",
      },
    ];
    this.props.DataJenis.forEach((list) => {
      let row = {
        value: list.kode_dept,
        name: list.kode_dept,
      };
      DataJenis.push(row);
    });
    const columns = [
      {
        dataField: "no_terima",
        text: "No Terima",
      },
      {
        dataField: "kode_group",
        text: "Kategori",
      },
      {
        dataField: "jenis",
        text: "Jenis",
      },

      {
        dataField: "berat",
        text: "Berat",
        headerClasses: 'text-right',
        formatter: (cell) => {
          return <div className="text-right">{ cell.toFixed(2) }</div>
        },
      },
    ];
    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off">
        <div className="row">
          <div className="col-lg-3">
            <Field
              name="tgl_awal"
              component={InputDate}
              label="Tanggal Dari"
              type="text"
              selected={this.state.tgl_awal}
              onChange={(date) => this.setStartDate(date)}
              placeholder="Masukan Tanggal Dari"
            />
          </div>

          <div className="col-lg-3">
            <Field
              name="tgl_akhir"
              component={InputDate}
              type="text"
              selected={this.state.tgl_akhir}
              onChange={(date) => this.setLastDate(date)}
              label="Tanggal Akhir"
              placeholder="Masukan Tanggal Akhir"
            />
          </div>
          <div className="col-lg-3">
            <Field
              name="kode_dept"
              component={ReanderSelect}
              type="text"
              options={DataJenis}
              label="Kode Dept"
              placeholder="Masukan Kode Dept"
            />
          </div>

          <div className="col-lg-3">
            <div className="text-right">
              <label>&nbsp;</label>
              <button
                type="submit"
                value="PDF"
                name="type_btn"
                className="btn btn-primary btn-block"
              >
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Mencari
                  </>
                ) : (
                  "Cari Laporan"
                )}
              </button>
            </div>
          </div>

          <div className="col-12">
            <Tabel
              keyField="_id"
              data={this.props.export ? this.props.databarang : []}
              columns={columns}
              empty={true}
              textEmpty="Data Laporan Terima Barang Kosong"
            />
          </div>
          <div className={this.props.export ? "col-lg-12" : "col-lg-12 d-none"}>
            {/* <label htmlFor="">&nbsp;</label> */}
            <div className="row">
              <div className="col-lg-6">
                <button
                  type="button"
                  onClick={() => this.exportPdf()}
                  className="btn btn-warning btn-block"
                >
                  {" "}
                  Export PDF{" "}
                </button>
              </div>
              <div className="col-lg-6">
                <LaporanTerimaBarangSupplierExel data={this.props.databarang} />
              </div>
            </div>
          </div>
        </div>
        {this.props.isLoading ? (
          <Loading
            loading
            background="rgba(0, 0, 0, 0.35)"
            loaderColor="rgba(94, 147, 117, 1)"
          />
        ) : null}
      </form>
    );
  }
}

HeadLaporanTerimaBarang = reduxForm({
  form: "HeadLaporanTerimaBarang",
  enableReinitialize: true,
  validate: ValidasiTanggalLaporan,
})(HeadLaporanTerimaBarang);
// export default connect()(HeadLaporanTerimaBarang);
export default connect((state) => {
  return {
    DataJenis: state.datamaster.getDataJenis,
    initialValues: {
      tgl_awal: getTglSystem(),
      tgl_akhir: getTglSystem(),
    },
  };
})(HeadLaporanTerimaBarang);
