import React, { Component } from "react";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import {
  getUserdata,
  localStoragedecryp,
} from "../../../../../components/helpers/function";

class LaporanTerimaBarangSupplierExel extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
   
    return (
      <>
        <ReactHTMLTableToExcel
          id="test-table-xls-button"
          className="btn btn-success btn-block"
          table="table-to-xls"
          filename="Laporan Terima Barang Suplier"
          sheet="Laporan Terima Barang Suplier"
          buttonText="Export Exel"
        />
        <table
          rowkey="kode_member"
          id="table-to-xls"
          style={{ display: "none" }}
        >
          <thead>
            <tr>
              <th colSpan="4"> Periode </th>
            </tr>
            <tr>
              <th colSpan="4">
                {" "}
                {localStoragedecryp("tgl_laporan").length === 0
                  ? null
                  : JSON.parse(localStoragedecryp("tgl_laporan")).tgl_awal +
                    "s/d" +
                    JSON.parse(localStoragedecryp("tgl_laporan"))
                      .tgl_akhir}{" "}
              </th>
            </tr>
            <tr>
              <th colSpan="4"> TOKO : {getUserdata().nama_toko} </th>
            </tr>

            <tr>
              <th colSpan="4"> ALAMAT : {getUserdata().alamat_toko} </th>
            </tr>
            <tr>
              <th colSpan="4"></th>
            </tr>
            <tr>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                NO TERIMA
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                KODE KATEGORI
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                JENIS
              </th>

              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                BERAT
              </th>
            </tr>
          </thead>
          <tbody>
            {this.props.data.map((row, no) => (
              <tr key={no}>
                <td > {row.no_terima}</td>
                <td> {row.kode_group}</td>
                <td> {row.jenis}</td>
                <td style={{ textAlign: "right"}}> {row.berat.toFixed(2)}</td>
              </tr>
            ))}
          </tbody>
          <tfoot>
            <tr>
              <th
                colSpan="3"
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  textAlign: "center",
                }}
              >
                {" "}
                Total{" "}
              </th>
              <th
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  textAlign: "right",
                }}
              >
                {this.props.data.reduce((a,b)=>a+b.berat,0).toFixed(2)}
              </th>
             
            </tr>
          </tfoot>
        </table>
      </>
    );
  }
}
export default LaporanTerimaBarangSupplierExel;
